import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { LoginForm, ProFormText } from '@ant-design/pro-components';
import { history, useModel } from '@umijs/max';
import { Alert, message } from 'antd';
import React, { useState } from 'react';
import { login } from '@/services/ant-design-pro/api';

const LoginMessage: React.FC<{
  content: string;
}> = ({ content }) => {
  return (
    <Alert
      style={{
        marginBottom: 24,
      }}
      message={content}
      type="error"
      showIcon
    />
  );
};

const Login: React.FC = () => {
  const [userLoginState, setUserLoginState] = useState<any>(null);
  const [type, setType] = useState<string>('account');
  const [loading, setLoading] = useState(false);
  const { initialState, setInitialState } = useModel('@@initialState');

  const handleSubmit = async (values: { username: string; password: string }) => {
    if (loading) {
      return; // Èò≤Ê≠¢ÈáçÂ§çÊèê‰∫§
    }
    
    console.log('üöÄ ÂºÄÂßãÁôªÂΩïÊµÅÁ®ã...', values);
    setLoading(true);
    try {
      // ÁôªÂΩï
      const msg = await login({ ...values, type }) as any;
      console.log('üì° ÁôªÂΩïAPIÂìçÂ∫î:', msg);
      console.log('üì° ÂìçÂ∫îÁ±ªÂûã:', typeof msg);
      console.log('üì° ÂìçÂ∫îÂÜÖÂÆπ:', JSON.stringify(msg, null, 2));
      
      if (msg && msg.code === 200) {
        const defaultLoginSuccessMessage = 'ÁôªÂΩïÊàêÂäüÔºÅ';
        message.success(defaultLoginSuccessMessage);
        
        console.log('üîê ÁôªÂΩïÊàêÂäüÔºåÂºÄÂßãÂ§ÑÁêÜË∑≥ËΩ¨ÈÄªËæë...');
        
        // ‰øùÂ≠òtokenÂà∞localStorage
        if (msg.data?.token) {
          localStorage.setItem('token', msg.data.token);
          console.log('‚úÖ TokenÂ∑≤‰øùÂ≠òÂà∞localStorage');
        }
        if (msg.data?.refreshToken) {
          localStorage.setItem('refreshToken', msg.data.refreshToken);
          console.log('‚úÖ RefreshTokenÂ∑≤‰øùÂ≠òÂà∞localStorage');
        }
        
        // Êõ¥Êñ∞initialState‰∏≠ÁöÑÁî®Êà∑‰ø°ÊÅØ
        if (msg.data?.user) {
          console.log('üë§ Áî®Êà∑‰ø°ÊÅØ:', msg.data.user);
          await setInitialState((s) => ({
            ...s,
            currentUser: msg.data.user,
          }));
          console.log('‚úÖ Áî®Êà∑Áä∂ÊÄÅÂ∑≤Êõ¥Êñ∞');
          
          // Á≠âÂæÖÁä∂ÊÄÅÊõ¥Êñ∞ÂÆåÊàêÂêéÂÜçË∑≥ËΩ¨
          setTimeout(() => {
            // Ë∑≥ËΩ¨Âà∞È¶ñÈ°µÊàñÊåáÂÆöÈ°µÈù¢
            const urlParams = new URL(window.location.href).searchParams;
            let redirect = urlParams.get('redirect') || '/dashboard';
            
            // Â¶ÇÊûúredirectÂ∑≤ÁªèÂåÖÂê´/data-platformÂâçÁºÄÔºåÈúÄË¶ÅÂéªÈô§‰ª•ÈÅøÂÖçÈáçÂ§ç
            if (redirect.startsWith('/data-platform')) {
              redirect = redirect.substring('/data-platform'.length) || '/dashboard';
            }
            
            // Á°Æ‰øùredirect‰ª•/ÂºÄÂ§¥
            if (!redirect.startsWith('/')) {
              redirect = '/' + redirect;
            }
            
            console.log('üîÑ ÂáÜÂ§áË∑≥ËΩ¨Âà∞:', redirect);
            
            console.log('üöÄ ÊâßË°åË∑≥ËΩ¨...');
            try {
              history.push(redirect);
              console.log('‚úÖ Ë∑≥ËΩ¨ÊàêÂäü');
            } catch (error) {
              console.error('‚ùå Ë∑≥ËΩ¨Â§±Ë¥•:', error);
              // Â§áÁî®ÊñπÊ°àÔºö‰ΩøÁî®window.location
              window.location.href = redirect;
            }
          }, 500); // Â¢ûÂä†Âª∂ËøüÁ°Æ‰øùÁä∂ÊÄÅÊõ¥Êñ∞ÂÆåÊàê
        } else {
          console.error('‚ùå Ê≤°ÊúâÁî®Êà∑‰ø°ÊÅØÔºåÊó†Ê≥ïË∑≥ËΩ¨');
        }
        
        return;
      }
      
      // Â¶ÇÊûúÂ§±Ë¥•ÂéªËÆæÁΩÆÁî®Êà∑ÈîôËØØ‰ø°ÊÅØ
      setUserLoginState(msg);
    } catch (error) {
      const defaultLoginFailureMessage = 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØïÔºÅ';
      console.log(error);
      message.error(defaultLoginFailureMessage);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        height: '100vh',
        overflow: 'auto',
        backgroundImage:
          "url('https://mdn.alipayobjects.com/yuyan_qk0oxh/afts/img/V-_oS6r-i7wAAAAAAAAAAAAAFl94AQBr')",
        backgroundSize: '100% 100%',
      }}
    >
      <div
        style={{
          flex: '1',
          padding: '32px 0',
        }}
      >
        <LoginForm
          contentStyle={{
            minWidth: 280,
            maxWidth: '75vw',
          }}
          logo={<img alt="logo" src="/data-platform/logo.svg" />}
          title="GDP‰Ωú‰∏öË∞ÉÂ∫¶Á≥ªÁªü"
          subTitle="‰ºÅ‰∏öÁ∫ßÂàÜÂ∏ÉÂºè‰ªªÂä°Ë∞ÉÂ∫¶Âπ≥Âè∞"
          initialValues={{
            autoLogin: true,
          }}
          onFinish={async (values) => {
            await handleSubmit(values as { username: string; password: string });
          }}
        >
          {userLoginState && userLoginState.code !== 200 && userLoginState.message && (
            <LoginMessage content={userLoginState.message} />
          )}

          <ProFormText
            name="username"
            fieldProps={{
              size: 'large',
              prefix: <UserOutlined />,
            }}
            placeholder="Áî®Êà∑Âêç: admin or user"
            rules={[
              {
                required: true,
                message: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç!',
              },
            ]}
          />
          <ProFormText.Password
            name="password"
            fieldProps={{
              size: 'large',
              prefix: <LockOutlined />,
            }}
            placeholder="ÂØÜÁ†Å: 123456"
            rules={[
              {
                required: true,
                message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†ÅÔºÅ',
              },
            ]}
          />

          <div
            style={{
              marginBottom: 24,
            }}
          >
            <a
              style={{
                float: 'right',
              }}
            >
              ÂøòËÆ∞ÂØÜÁ†Å
            </a>
          </div>
        </LoginForm>
      </div>
    </div>
  );
};

export default Login;
